@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@100;400;900&family=Open+Sans:wght@300;400;800&family=Roboto:wght@100;300;400;500;900&display=swap);
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
/* Universal classes */
.margin-top {
  margin-top: 100px;
}

.center {
  text-align: center;
}

.icon {
  display: inline;
  -webkit-transform: translateY(2px);
          transform: translateY(2px);
}

.px-3 {
  padding: 0 0.5em;
}

body,
html {
  scroll-behavior: smooth;
  height: 100%;
  width: 100%;
  line-height: 1.7;
  font-family: "Open Sans", sans-serif;
}

/* Media queries for the entire application */
@media screen and (min-width: 975px) {
  .home-container {
    display: -webkit-flex;
    display: flex;
    margin-top: 0;
  }
  .content {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    height: 60vh;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
  }
  .img-container img {
    height: 100%;
  }
}

.navbar {
  display: -webkit-flex;
  display: flex;
  /* position: relative; */
  padding: 25px 30px;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  color: rgb(69, 143, 100);
  box-shadow: 0 0.5rem 1rem rgba(43, 47, 43, 0.057),
    inset 0 -1px 0 rgba(0, 0, 0, 0.08);
}

.nav-items {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
}

.menu-button:hover {
  background: rgba(69, 143, 100, 0.171);
}

.menu-button,
.logo {
  padding: 0.4em;
}

.logo {
  font-family: "Open Sans", sans-serif;
  font-weight: 900;
  text-decoration: none;
  color: rgb(69, 143, 100);
}

.menu-button *,
.search-icon {
  font-size: 20px;
  height: 90%;
}

.nav-menu input[type="search"],
.nav-menu a {
  padding: 0.4em;
  border: none;
  outline: none;
  border-radius: 2px;
  margin: 0 10px;
}

.nav-menu input[type="search"] {
  border: 1px solid rgb(69, 143, 100);
  padding: 0.55em;
}

.nav-menu a {
  text-decoration: none;
  color: #fff;
  background-color: rgb(69, 143, 100);
}
.search-icon {
  text-align: center;
}

/* Styles for side menu */
.side-menu {
  width: 0px;
  z-index: 1;
  position: fixed;
  overflow: hidden;
  background-color: rgb(69, 143, 100);
  height: 100%;
  transition: all 0.3s ease-in-out;
}

.side-menu-items {
  display: -webkit-flex;
  display: flex;
  height: 80%;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.side-menu-items .links * {
  margin: 6px 5px;
  font-size: 15px;
}

.side-menu-items .links *,
.start-menu-item * {
  display: block;
  color: #fff;
  text-decoration: none;
}

.login-btn {
  color: rgb(69, 143, 100);
  text-align: center;
  background-color: rgb(255, 255, 255);
  padding: 1px 6px;
  border-radius: 2px;
}
.login-btn * {
  -webkit-transform: translateY(2px);
          transform: translateY(2px);
  color: rgb(69, 143, 100);
  display: inline;
}
.active-side-menu-item {
  /* border-left: 5px solid #fff; */
  padding: 3px 0 3px 6px;
  margin: 0 0px;
  border-radius: 0px;
  background-color: rgba(202, 212, 206, 0.568);
}
.show-width {
  padding: 20px;
  width: 200px;
}

.content {
  padding: 30px 30px;
  text-align: center;
  margin-bottom: 30px;
}

.content h1 {
  font-size: 30px;
  font-weight: 900;
  line-height: 1.5;
}

.content h1 span {
  color: rgb(69, 143, 100);
}

.content p {
  font-size: 15px;
  padding: 0 20px;
  margin-bottom: 20px;
}

.img-container img {
  width: 100%;
}

.btn {
  display: block;
  border: none;
  background: none;
  padding: 10px;
  width: 100%;
  text-transform: uppercase;
  border-radius: 2px;
  margin: 8px 0;
  cursor: pointer;
}
.btn-primary {
  background: rgb(69, 143, 100);
  color: #fff;
}
.btn-secondary {
  color: rgb(69, 143, 100);
  border: 1px solid rgb(69, 143, 100);
}

.btn-google {
  color: #fff;
  background: #4286f4;
  border: 1px solid rgb(27, 129, 197);
}

.showcase {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100vw;
  height: 100%;
  background: #fff;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.close-showcase {
  font-size: 30px;
  font-weight: 900;
  position: absolute;
  top: 20px;
  right: 20px;
  color: rgb(69, 143, 100);
  cursor: pointer;
}
.close-showcase:hover {
  background: rgba(69, 143, 100, 0.171);
}
.showcase-options h1 {
  color: rgb(69, 143, 100);
}

.showcase-options .options {
  color: rgba(53, 73, 61, 0.712);
  width: 250px;
  text-decoration: none;
  padding: 20px 0;
  display: block;
  cursor: pointer;
  border-bottom: 1px solid rgb(42, 161, 92);
  margin-bottom: 30px;
}

.showcase-options .options h2 * {
  font-size: 30px;
  position: relative;
  -webkit-transform: translateY(2px);
          transform: translateY(2px);
  right: -30px;
}

.showcase-options .options:hover {
  border-bottom: 1px solid rgb(6, 44, 22);
  color: rgb(53, 73, 61);
  transition: border-bottom ease-in 0.2s;
}

.form {
  width: 350px;
  margin: 0 auto;
  height: 65vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  padding: 40px 0;
}

.group-input {
  width: 100%;
  display: block;
}

.group-input label {
  font-size: 13px;
}

.group-input h3,
.group-input h2,
.group-input small {
  color: rgb(69, 143, 100);
}
.group-input h3 {
  font-weight: 800;
  font-size: 30px;
}

.group-input a {
  font-size: 12px;
  color: gray;
  text-decoration: none;
}

.group-input a:hover {
  text-decoration: underline;
}

.group-input label,
.group-input input {
  color: rgb(112, 112, 112);
  display: block;
  width: 100%;
  border-radius: 2px;
  margin: 3px 0;
}

.group-input input {
  padding: 10px;
  font-size: 16px;
  outline: none;
  border: 0.1em solid rgba(69, 143, 100, 0.418);
}

.tournaments {
  margin: 10px;
}

.tournaments-header {
  color: rgb(69, 143, 100);
}

.tournament-buttons button {
  border: none;
  padding: 2px 7px;
  border: 1px solid rgb(69, 143, 100);
  color: rgb(69, 143, 100);
  background: #fff;
  margin: 2px;
}
.active {
  background: rgb(69, 143, 100) !important;
  color: #fff !important;
}

.tournaments-list {
  margin: 10px 0;
}

.tournament {
  font-weight: 100;
  box-shadow: 1px 1px 0.55rem rgba(0, 0, 0, 0.219);
  padding: 20px;
  margin: 20px 0;
  background: linear-gradient(
    to bottom,
    rgba(69, 143, 100, 0.883),
    rgba(69, 143, 100, 0.726),
    rgba(69, 143, 100, 0.89)
  );
  display: block;
  text-decoration: none;
  color: #fff;
}

.tournament h2 {
  font-size: 18px;
}

.tournament p {
  font-size: 15px;
}
.tournament h6 {
  font-size: 12px;
  font-weight: normal;
}
.tournament-date h4 {
  font-weight: normal;
  font-size: 12px;
}
.tournament-details {
  margin: 20px 20px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}
.prize {
  margin-bottom: 15px;
}
.prize .icon {
  color: rgb(69, 143, 100);
}
.location {
  margin: 6px 0;
}
.prize h6,
.location h6 {
  font-size: 13px;
  font-weight: normal;
}
.details h3,
.details > h4 {
  color: rgb(69, 143, 100);
}
.details h4 {
  margin: 5px 0;
}
.details hr {
  border: 1px solid rgba(0, 7, 3, 0.171);
}

.details p {
  color: rgb(7, 41, 21);
  font-size: 14px;
}
.location a,
.join-tournament a {
  text-decoration: none;
  color: rgb(69, 143, 100);
  font-size: 12px;
}

.join-tournament small {
  display: block;
}

.join-tournament span {
  font-size: 14px;
  padding-bottom: 3px;
}

